import React, { useEffect, useState } from 'react';
import millify from 'millify';
import { Typography, Row, Col, Statistic, Button } from 'antd';
import { Link } from 'react-router-dom';

import { getAllConsigment, getAllStats, getAllConsigmentWithFilter, filterConsignments } from '../api/index.js';
import Consigments from './Consignments.js';
import Godowns from './Godowns.js';
import Items from './Items.js';
import Suppliers from './Suppliers.js';
import InputField from './common/InputField.js';
import Filter from './Filter.js';
import { SearchOutlined } from '@ant-design/icons';
import Challans from './Challans.js';

const { Title } = Typography;

const Homepage = () => {

  const [consigments, setConsignments] = useState([]);
  const [pageDetails, setPageDetails] = useState({ current: 1, pageSize: 10, total: 10});
  const [globalStats, setStats] = useState({
      itemCount: 0,
      consignmentCount: 0,
      packageCount: 0,
      supplierCount: 0,
      godownCount: 0
  });
  const [filterApplied, setFilterApplied ] = useState(false);
  const [filterBody, setFilterBody] = useState({});
  useEffect(() => {
    fetchConsignments();
  },[]);

  const fetchConsignments = async (offset = 0, limit = 10) => {
    setFilterApplied(false);
    const sValue = document.getElementById('searchByConNo').value;

    const response = await getAllConsigment(sValue,offset, limit);
    console.log(response)
    setConsignments(response?.data?.entity.rows);
    setPageDetails({ current: offset / limit + 1, pageSize: limit, total: response?.data?.entity.count });

    const statsResponse = await getAllStats();
    console.log(statsResponse)
    setStats(statsResponse?.data?.entity);
    
  }

  const filterCallback = async (offset = 0, limit = 10, filter) => {
    const response = await filterConsignments(filter ? filter : filterBody, offset, limit);
    console.log(response)
    setConsignments(response?.data?.entity.rows);
    setPageDetails({ current: offset / limit + 1, pageSize: limit, total: response?.data?.entity.count });
    if(filter){
      setFilterApplied(true);
      setFilterBody(filter);
    }
  }

  const serachConsignment = async (value) => {
    await fetchConsignments();
  }

  const handleKeyPress = async (event) => {
    if(event.key === 'Enter'){
      await fetchConsignments();
    }
  }


  return (
    <>
      <Title level={2} className="heading" id='mainheader'>Stock Management Stats</Title>
      <Row gutter={[32, 32]}>
            <Col span={12}><Statistic title="Total Consigments" value={globalStats?.consignmentCount ? globalStats.consignmentCount : 0 }/></Col>
            <Col span={12}><Statistic title="Total Packages" value={millify(globalStats?.packageCount ? globalStats.packageCount : 0)}/></Col>
            <Col span={12}><Statistic title="Total Items" value={millify(globalStats?.itemCount? globalStats.itemCount : 0)}/></Col>
            <Col span={12}><Statistic title="Total Godowns" value={millify(globalStats?.godownCount ? globalStats.godownCount : 0)}/></Col>
            <Col span={12}><Statistic title="Total Suppliers" value={millify(globalStats?.supplierCount ? globalStats.supplierCount : 0)}/></Col>
      </Row>
      
      <Row id='searchContainer'>
        <InputField
              label={'Search'}
              placeholder={'consignment no, transporter, supplier, item, bill no, mr no,godown'}
              type="text"
              name="searchByConNo"
              id="searchByConNo"
              onKeyPress={handleKeyPress}
              icol={10}
          />
          <Col span={4}>
                <Button type="primary" className='mt-3 ml-4' onClick={(event) => serachConsignment(event.target.value)} title="Search" >
                    <SearchOutlined /> Search
                </Button>
          </Col>
      </Row>
      <Filter filterCallback={filterCallback} fetchConsignments={fetchConsignments} setFilterApplied={setFilterApplied} />

      <div id='consignments'>
        <Consigments filterCallback={filterCallback} consigments={ consigments } fetchConsignments={fetchConsignments} filterApplied={filterApplied} pageDetails={pageDetails}/>
      </div>
      
      <div id='challans'>
        <Challans consigments={ consigments }/>
      </div>

      <div id='godowns'>
        <Godowns/>
      </div>
      <div id='items'>
        <Items/>
      </div>
      <div id='suppliers'>
        <Suppliers/>
      </div>
    </>
  );
};

export default Homepage;
