import { CopyOutlined, DeleteOutlined, EditOutlined, HeatMapOutlined, PlusSquareOutlined } from "@ant-design/icons";
import { Button, Col, Popconfirm, Row, Table, Typography } from "antd";
import { useState } from "react";
import FieldData from '../data/FieldData';
import ConsignmentModal from './modals/ConsignmentModal';
import LocationModal from "./modals/LocationModal";
import { createConsignment, deleteConsignment } from "../api";
import BulkConsignmentModal from "./modals/BulkConsignmentModal";
const { Title } = Typography;
const Consigments = ({ consigments , fetchConsignments, filterApplied, pageDetails, filterCallback}) => {

    const [visibleCM, setVisibleCM] = useState(false);
    const [editData, setEditData] = useState(null);
    const [actionColumn,SetActionCol] = useState(true);
    const [visibleLM, setVisibleLM] = useState(false);
    const [editLData, setLEditData] = useState(null);
    const [visibleBCM, setVisibleBCM] = useState(false);
   

    const onEdit = (data) => {
        setEditData({...data})
        setVisibleCM(true);
    }

    const onAdd = () => {
        setEditData(null)
        setVisibleCM(true);
    }

    const onAddBulk = () => {
        setEditData(null)
        setVisibleBCM(true);
    }

    const  onDelete = async (data) => {
        const isConfirm = confirm('Are you sure you want to delete ?')
        if(isConfirm){
            await deleteConsignment(data.id);
            await fetchConsignments();
        } 
    }

    const onCopy = async(data) => {
        let response = await createConsignment(data);
        console.log(response);
        if(response?.data?.status == true){
            swal("Success" , "Succesfully copied and created the consignment");
        }
        else{
            swal("OOPS Something Went wrong", "error");
        }

        await fetchConsignments();
    }

    const onChangeLocation = (data) => {
        setEditData({...data})
        setVisibleLM(true);
    }
   
    const uniqueMap = consigments && consigments.length > 0 ? consigments.reduce((previous, current) => {
            for(let key in current){
                if(!previous[key]){
                    previous[key] = new Set();
                }
                previous[key].add(current[key]);
            }

            return previous;
    },{}) : {};

    const consignmentIndexMap = consigments && consigments.length > 0 ? consigments.reduce((previous, current, currentIndex) => {
            if(!previous[current.consignmentNo]){
                previous[current.consignmentNo] = [];
            }
            previous[current.consignmentNo].push(currentIndex);

            return previous;
    },{}) : {};

     let consignmentRowSpanMap = {};

   

    const columns = FieldData.map((column) => ({
        title:  ( 
            <Typography.Text ellipsis={false} title={column.label}>
                {column.label}
            </Typography.Text>
        ),
        dataIndex: column.name == 'godown' ? 'godownName' : column.name,
        key: column.name + (new Date().getTime() + Math.random() * 10000),
        width: '100px',
        filters: uniqueMap[column.name] ? Array.from(uniqueMap[column.name]).map((value) => ({ text: value, value: value})) : [],
        filterMode: 'tree',
        filterSearch: true,
        onFilter: (value, record) => record[column.name] ? record[column.name].startsWith(value) : false,
        onCell: (data, index) => {
            if((column.name == 'consignmentNo' || column.name == 'amount') && data.consignmentNo && consignmentIndexMap[data.consignmentNo]?.length > 1){
                if( consignmentIndexMap[data.consignmentNo].indexOf(index) == 0){
                    return {
                        rowSpan: consignmentIndexMap[data.consignmentNo]?.length,
                    }
                }
                else{
                    return {
                        rowSpan: 0,
                    }
                }
            }
            return {};
        }
    }));

    if(actionColumn){
        const actionCol = {
            title: 'Action',
            key: 'operation'  + (new Date().getTime() + Math.random() * 10000),
            fixed: 'right',
            width: 250,
            render: (data) => {
                return (
                    <>
                        <Button key={'edit'+data.id} type="primary" onClick={() => onEdit(data)} title="Edit">
                            <EditOutlined />
                        </Button>
                        
                        <Button key={'delete'+data.id} type="secondary" onClick={() => onDelete(data)} className="ml-2" title="Delete">
                            <DeleteOutlined />
                        </Button>
    
                        <Button key={'location'+data.id} type="secondary" onClick={() => onChangeLocation(data)} className="ml-2" title="Delete">
                            <HeatMapOutlined />
                        </Button>

                        <Button key={'copy'+data.id} type="dashed" onClick={() => onCopy(data)} className="ml-2" title="Copy">
                            <CopyOutlined />
                        </Button>
                        
                    </>
                );
            },
        };
    
        columns.push(actionCol);
    }
    const searchValue = document.getElementById('searchByConNo')?.value;
    const getGodownName = (con) => {
        return con.godowns.length > 0 ? con.godowns[0].name: con.godown;
    }
    
    const pageSizeOptions = ['10', '20', '50', '100'];

    if(pageDetails.total > 100){
        pageSizeOptions.push(pageDetails.total + '');
    }

    const onPageChange = async(page, pageSize) => {
        if(filterApplied){
            await filterCallback((page - 1)*pageSize, pageSize)
            return;
        }
        await fetchConsignments((page - 1)*pageSize, pageSize);
    }

    return (
        <>
         <div className="home-heading-container">
            <Title level={2} className="home-title">Consignments</Title>
            <div>{filterApplied ? `(Filter Applied)` : (searchValue != '' ? ` (Search results - "${searchValue}")` : '')}</div>
            <Title level={3} className="show-more">
                <Button className="mr-4" onClick={() => onAddBulk()}><PlusSquareOutlined />Add Bulk Consigments</Button>
                <a className="text-sm mr-4" onClick={() => SetActionCol(!actionColumn)}>Action</a>
                <Button onClick={() => onAdd()}><PlusSquareOutlined />Add Consigments</Button>
            </Title>
        </div>
        <Row className="w-full">
            <Col span={24}>
                <Table
                    dataSource={consigments ? consigments.map((con,index) => ({ ...con,godownName: getGodownName(con), key: index + (new Date().getTime() + Math.random() * 10000)})) : null } 
                    columns={columns}
                    bordered
                    scroll={{ x: 1600 }}
                    pagination={{ position: ['bottomRight', 'topRight'], pageSizeOptions, onChange: onPageChange, ...pageDetails }}
                    rowKey={(record) => record.id + (new Date().getTime() + Math.random() * 10000)}
                />
            </Col>
        </Row>
        <BulkConsignmentModal visible={visibleBCM} setVisible={setVisibleBCM} fetchConsignments={fetchConsignments} />
        <ConsignmentModal visible={visibleCM} setVisible={setVisibleCM} data={editData} fetchConsignments={fetchConsignments} />
        <LocationModal visible={visibleLM} setVisible={setVisibleLM} consigmentData = {editData} fetchConsignments={fetchConsignments}/>
        </>
    )
}

export default Consigments;