import React, { useEffect, useState } from 'react';
import { Modal, Button, Row, Col, Table, Typography, Tabs } from 'antd';
import InputField from '../common/InputField';
import { createChallan, getAllGodowns, getConsignmentsForChallan, getConsignmentsForLocation } from '../../api'; 
import moment from 'moment';
import swal from 'sweetalert';
import ChallanData from '../../data/ChallanData';
import SelectField from '../common/SelectField';
import Filter from '../Filter';
import ConChallanData from '../../data/ConChallanData';
import { DeleteOutlined } from '@ant-design/icons';

const { Title } = Typography;1
const { TabPane } = Tabs;


const ChallanModal = ({ visible , setVisible , data , callback}) => {

    const initialData = ChallanData.reduce((previous, field) => ({...previous,[field.name]: ''}),{});
    initialData.selectedRowKeys = [];
    initialData.selectedRows = [];
    const [formData, setFormData] = useState(initialData);
    const [godowns,setGodowns] = useState([]);
    const [consigments,setConsignments] = useState([]);

    const getGodowns = async () => {
        const list = await getAllGodowns();
        setGodowns(list?.data?.entity);
    }

    const clearData = () => {
        setFormData(initialData);
    }

    const setEditData = async (data) => {
        const selectedRowKeys = data.challanConsignments.map((obj) => obj.consignmentId);
        
        const editData = { fromGodownId: data.godownId, toGodownId: data.toGodownId , selectedRowKeys, selectedRows: []};
        const response = await getConsignmentsForChallan(data.id);
        if(response?.data?.entity){
            editData.selectedRows = getConsignmentObject(response?.data?.entity);
        }
        setFormData({...editData});
    }

    useEffect(() => {
        if (data) {
            setEditData(data)
        }
        else{
            clearData();
        }
        
        getGodowns();
    },[visible,data])

    const onSubmit = async (close) => {
        const { fromGodownId, toGodownId, selectedRowKeys } = formData;
        const fromGodown = godowns.find((godown) => godown.name == fromGodownId);
        const toGodown = godowns.find((godown) => godown.name == toGodownId);
       
        const data = { fromGodownId: fromGodown.id, toGodownId: toGodown.id, consigments: selectedRowKeys };
        let response = await createChallan(data);
        console.log(response)
        if(response?.data?.status == true){
            swal("Succesfully generated challan with Challan Number " + response?.data?.entity?.challan.number, "success");
        }
        else{
            swal("OOPS Something Went wrong", "error");
        }

        if(close){
            setVisible(false);
            clearData()
        } 
        

        await callback();
    }


    const getConsignmentObject = (con) => {
        return con ? con.map(({ id, consignmentNo, privartMark, item, rate },index) => 
        ({ id, consignmentNo,  privartMark, item, rate, key: index + (new Date().getTime() + Math.random() * 10000)})) : null;
    }

    const onSelectChange = async (name, value, manual, editData) => {
        if(name === 'fromGodownId'){
            const fromGodown = godowns.find((godown) => godown.name == value);
            if(fromGodown){
                const response = await getConsignmentsForLocation(fromGodown.id);


                if(response?.data?.entity?.length > 0){
                    const consi = response?.data?.entity.map((data) => data.consignments);
                    if(manual){
                        let selectedRows = consi.filter((obj) => data.challanConsignments.findIndex((cc) => cc.consignmentId == obj.id) >= 0);
                        console.log(getConsignmentObject(selectedRows));
                        setConsignments(consi);
                        return;
                    }
                    else{
                        setConsignments(consi);
                    }
                }
                else{
                    setConsignments([]);
                }
            }
            
        }
       
        setFormData({...formData,[name]: value});
       
    }

    const uniqueMap = consigments && consigments.length > 0 ? consigments.reduce((previous, current) => {
            for(let key in current){
                if(!previous[key]){
                    previous[key] = new Set();
                }
                previous[key].add(current[key]);
            }

            return previous;
    },{}) : {};

    const columns = ConChallanData.map((column) => ({
        title:  ( 
            <Typography.Text ellipsis={false} title={column.label}>
                {column.label}
            </Typography.Text>
        ),
        dataIndex: column.name,
        key: column.name + (new Date().getTime() + Math.random() * 10000),
        width: '100px'
    }));

    const rowSelection = {
        selectedRowKeys: formData.selectedRowKeys,
        onChange: (selectedRowKeys, selectedRows) => {
            setFormData({...formData, selectedRowKeys,selectedRows});
        }
    };

    const onRemove = (data) => {
        const index = formData.selectedRows.findIndex((row) => row.id == data.id)
        formData.selectedRows.splice(index, 1);
        setFormData({...formData, selectedRowKeys: formData.selectedRows.map((row) => row.id),selectedRows});
    }

    const onTabChange = (key) => {
        //console.log(key);
    };

    const removeCol = {
        title: 'Remove Selection',
        key: 'operation'  + (new Date().getTime() + Math.random() * 10000),
        fixed: 'right',
        width: 10,
        render: (data) => {
            return (
                <>
                    <Button key={'delete'+data.id} type="secondary" onClick={() => onRemove(data)} className="ml-2" title="Delete">
                        <DeleteOutlined />
                    </Button>
                    
                </>
            );
        },
    };


    return (
        <>
        <Modal
            title={`${data ? 'Edit' : 'Generate'} Challan`}
            visible={visible}
            width={"80%"}
            style={{ top: 10 }}
            keyboard={false}
            onCancel={() => setVisible(false)}
            footer={data ? null : [
                <Button key="cancel" onClick={() => setVisible(false)}>
                    Cancel
                </Button>,
                <Button key="save" type="primary" onClick={() => onSubmit()}>
                    Save
                </Button>,
                <Button key="savclose" type="primary" onClick={() => onSubmit(true)}>
                    Save and Close
                </Button>,
                ]}
        >
            <div>
                <Row className='border-b-2 m-b-5'>
                    { 
                        ChallanData.map((field) =>
                        <SelectField
                            label={field.label}
                            option={eval(field.list).map((item) => ({ value: item.name, text: item.name}))}
                            showSearch
                            optionFilterProp="children"
                            value={formData ? formData[field.name] : ''}
                            showArrow={false}
                            onChange={(value) => onSelectChange(field.name, value)}
                            filterOption={(input, option) =>
                                option.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                            }
                            filterSort={(optionA, optionB) =>
                                optionA.children.toLowerCase().localeCompare(optionB.children.toLowerCase())
                            }
                            key={field.name}
                        />
                        )
                    }     
                </Row>


                <Tabs defaultActiveKey={data ? '2' : '1'} onChange={onTabChange}>
                    { data? null : <TabPane tab="Select Consignments" key="1" >
                        <Row>
                        {
                            formData.fromGodownId == '' ?
                            <Col span={24}> <h3 className='text-center'> Please select source godown</h3> </Col> :
                            <Col span={24}>
                                 <Table
                                    dataSource={
                                        getConsignmentObject(consigments)
                                    } 
                                    columns={columns}
                                    bordered
                                    pagination={{}}
                                    rowKey={(record) => `${record.id}`}
                                    rowSelection={rowSelection}
                                />
                            </Col>
                        }
                    </Row>
                    </TabPane> }
                    <TabPane tab="Selected Consignments" key="2">
                       
                        {
                            formData.selectedRows.length == 0 ?
                            <Col span={24}> <h3 className='text-center'>  No Consignments Selected  </h3> </Col> :
                            <Col span={24}>
                                 <Table
                                    dataSource={
                                        formData.selectedRows
                                    } 
                                    columns={data? columns : [...columns,removeCol]}
                                    bordered
                                    pagination={{ }}
                                    rowKey={(record) => `${record.id}`}
                                />
                            </Col>
                        }
                    </TabPane>
                </Tabs>
            </div>
                   
        </Modal>
        </>
    );
};

export default ChallanModal;