import React, { useEffect, useState } from 'react';
import { Modal, Button, Row, Col, Table, Typography } from 'antd';
import { getChallansForConsignments } from '../../api'; 
import moment from 'moment';
import ChallanData from '../../data/ChallanDataAlt';
const { Title } = Typography;

const LocationModal = ({ visible , setVisible , consigmentData}) => {

    const [consigment,setConsigment] = useState(consigmentData);
    const [challans,setChallans] = useState([]);

    const getChallansWithConsignmentId = async (consigment) => {
        const response = await getChallansForConsignments(consigment.id);

        console.log(response);
        if(response?.data?.entity){
            const datas = response?.data?.entity;
            
            setChallans(datas.map((data) => (
                {
                    number: data.challan.number,
                    godownId: data.fromGodown.name,
                    toGodownId: data.toGodown.name
                }
            )));
        }
    }

    useEffect(() => {
        getChallansWithConsignmentId(consigmentData);
    },[consigmentData]);

    const fieldData = ChallanData;
    const columns = fieldData.map((column) => ({
        title:  ( 
            <Typography.Text ellipsis={true} title={column.label}>
                {column.label}
            </Typography.Text>
        ),
        dataIndex: column.name,
        key: column.name,
        width: '200px'
    }));



    return (
        <>
        <Modal
            title={`Track Consignment`}
            visible={visible}
            width={"45%"}
            style={{ top: 75 }}
            onCancel={() => setVisible(false)}
            footer={[
            <Button key="cancel" onClick={() => setVisible(false)}>
                Close
            </Button>
            ]}
        >
            <div>
               
                <Row>
                    <Col span={24} className='my-5 border-b-4'>
                    <   Title level={4} className="home-title"> Current Godown : { challans.length > 0 ? challans[challans.length - 1].toGodownId: consigmentData?.godown}</Title> 
                    </Col>

                    <Title level={5} className="home-title">Consignment's Track History</Title> 
                    <Col span={24}>
                        <Table
                            columns={columns}
                            dataSource={challans}
                            bordered
                            pagination={ {}}
                        />
                    </Col>
                </Row>
            </div>
                   
        </Modal>
        </>
    );
};

export default LocationModal;