import { DeleteOutlined, EditOutlined, FundViewOutlined, PlusSquareOutlined, ReadOutlined } from "@ant-design/icons";
import { Button, Col, Row, Table, Typography } from "antd";
import { useEffect, useState } from "react";

import { getAllChallans } from "../api";
import ChallanData from "../data/ChallanDataAlt";
import ChallanModal from "./modals/ChallanModal";
const { Title } = Typography;
const Challans = ({ }) => {

    const [items, setChallans] = useState(null);
    const [visible, setVisible] = useState(false);
    const [editData, setEditData] = useState(null);

    const fetchChallans = async () => {
        const response = await getAllChallans();
        console.log(response);
        if(response?.data?.entity){
            const challans = response?.data?.entity;

            const data = challans.map((challan) => ({ ...challan, godownId: challan.godown.name, toGodownId: challan.toGodown.name}))
            setChallans(data);
        }
    }

    useEffect(() => {
        fetchChallans();
    },[]);


    const onEdit = (data) => {
        setEditData({...data})
        setVisible(true);
    }

    const onAdd = () => {
        setEditData(null)
        setVisible(true);
    }

    const onDelete = async (data) => {
        const isConfirm = confirm('Are you sure you want to delete ?')
        if(isConfirm){
            await deleteItem(data.id);
            fetchChallans();
            //refresh();
        } 
    }

    const fieldData = ChallanData;
    const columns = fieldData.map((column) => ({
        title:  ( 
            <Typography.Text ellipsis={true} title={column.label}>
                {column.label}
            </Typography.Text>
        ),
        dataIndex: column.name,
        key: column.name,
        width: '150px'
    }));

    columns.push({
        title: 'Action',
        key: 'operation',
        width: 10,
        render: (data) => {
            return (
                <div className="float-right">
                    <Button key={'edit'+data.id} type="primary" onClick={() => onEdit(data)} title="Edit">
                        <ReadOutlined />
                    </Button>
                    {/*                     
                    <Button key={'delete'+data.id} type="secondary" onClick={() => onDelete(data)} className="ml-2" title="Delete">
                        <DeleteOutlined />
                    </Button> */}
                </div>
            );
        },
    });

    return (
        <>
            <div className="home-heading-container">
                <Title level={2} className="home-title">Challans</Title>
                <Title level={3} className="show-more"><Button onClick={() => onAdd()}><PlusSquareOutlined />Generate Challans</Button></Title>
            </div>
            <Row className="w-full">
                <Col span={24}>
                    <Table
                        dataSource={items}
                        columns={columns}
                        bordered
                        pagination={ {}}
                    />
                </Col>
            </Row> 
            <ChallanModal visible={visible} setVisible={setVisible} data={editData} callback={fetchChallans}/>       
        </>
    );
}

export default Challans;

